@use '../alias/medias' as *;
@use '../alias/mixins' as *;

.section-heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: clamp(.5rem, 3vw, .75rem);
    text-align: center;

    &__suptitle {
        --_gap-width: .5rem;
        --_bar-width: 2rem;
        --_bar-height: .1rem;
        position: relative;
        padding-top: calc(var(--_bar-height) + var(--_gap-width));
        width: fit-content;
        color: var(--clr-ylw-100);
        font-weight: 500;
        font-size: .875rem;
        line-height: 1.2875;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            margin-inline: auto;
            width: var(--_bar-width);
            height: .1rem;
            background-color: currentColor;
        }
    }

    &__title {
        @include clampText(1.5rem, 3vw, 2rem);
        line-height: 1.25;
    }

    @include bp-up('tablet') {
        &:not(&--center) {
            flex-direction: column;
            align-items: start;
            text-align: start;

            .section-heading {
                &__suptitle {
                    padding-top: 0;
                    padding-left: calc(var(--_bar-width) + var(--_gap-width));
                    margin-left: calc((var(--_bar-width) + var(--_gap-width)) * -1);

                    &::before {
                        bottom: 0;
                        left: 0;
                        right: unset;
                        margin-inline: 0;
                        margin-block: auto;
                    }
                }
            }
        }
    }
}