$bp-up: (
    "small-phone": "375px",
    "phone": "480px",
    "phablet": "576px",
    "tablet": "768px",
    "laptop": "992px",
    "desktop": "1200px",
    "wide-desktop": "1400px",
);

$bp-down: (
    "small-phone": "374px",
    "phone": "479px",
    "phablet": "575px", 
    "tablet": "767px",
    "laptop": "991px",
    "desktop": "1199px",
    "wide-desktop": "1399px",
);

@mixin bp-up($size) { 
    @media (min-width: map-get($bp-up, $size)) {
        @content;
    }
}

@mixin bp-down($size) { 
    @media (max-width: map-get($bp-down, $size)) {
        @content;
    }
}

