.slider {
    position: relative;
}

.slider__wrapper {

}

.slider__list {
}

.slider__slide {

}

.slider__pagination {
    position: absolute;
    display: flex;
    align-items: center;
    gap: .5rem;
    bottom: 0;
    left: 0;
    right: 0;
    width: fit-content;
    margin-inline: auto;
}

.slider__pagination-dot {
    --_aspect-ratio: .5rem;
    width: var(--_aspect-ratio);
    height: var(--_aspect-ratio);
    background-color: #E0E3EB;
    border-radius: 50%;
}

.slider__pagination-dot[slider-pagination-dot='active'] {
    background-color: #3C4563;
}