@use '../alias/medias' as *;

.burger {
    &__btn {
        --_aspect-ratio: 2rem;
        --_block-position: calc(var(--_aspect-ratio) / 4);
        --_bar-width: 50%;
        --_bar-height: 2px;
        --_transition-time: .2s;
        position: relative;
        padding: 0;
        width: var(--_aspect-ratio);
        height: var(--_aspect-ratio);
        box-sizing: content-box;

        span {
            position: absolute;
            display: inline-block;
            inset: 0;
            margin: auto;
            width: var(--_bar-width);
            height: var(--_bar-height);

            &::before,
            &::after {
                content: '';
                position: absolute;
                inset: 0;
                background-color: currentColor;
                transition: rotate var(--_transition-time);
            }
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            margin-inline: auto;
            width: var(--_bar-width);
            height: var(--_bar-height);
            background-color: currentColor;
            transition-property: opacity, left, right;
            transition-duration: var(--_transition-time);
        }

        &::before {
            top: var(--_block-position);
        }
        &::after {
            bottom: var(--_block-position);
        }
    }

    &__content {
        --_display-option: none;
        display: var(--_display-option);
    }

    &.active {
        .burger {
            &__btn {
                &::before,
                &::after {
                    --_disapear-pos: -50%;
                    opacity: 0;
                }
                &::before {
                    left: var(--_disapear-pos);
                }
                
                &::after {
                    right: var(--_disapear-pos);
                }

                span {
                    &::before {
                        rotate: -135deg;
                    }

                    &::after {
                        rotate: -225deg;
                    }
                }
            }
            &__content {
                --_display-option: block;
            }
        }
    }

    @include bp-up('tablet') {
        &__btn {
            display: none;
        }

        &__content {
            --_display-option: block;
        }
    }
}