@use '../alias/medias' as *;
@use '../alias/mixins' as *;

.btn {
    --_pd-block: .75rem;
    --_pd-inlie: 1rem;
    display: inline-flex;
    align-items: center;
    gap: clamp(.5rem, 3vw, 1rem);
    padding: var(--_pd-block) var(--_pd-inlie);
    width: fit-content;
    background-color: var(--clr-grn-200);
    border-radius: 2rem;
    border: 1px solid transparent;
    color: var(--clr-ntrl-100);
    @include clampText(.75rem, 3vw, 1rem);
    font-weight: 600;

    svg {
        fill: currentColor;
        stroke: currentColor;
    }

    &--light {
        background-color: var(--clr-grn-100);
        color: var(--clr-grn-300);
    }

    &--transparent {
        --_bg-alpha: .1;
        --_bd-alpha: .3;
        background-color: hsla(var(--hsl-ntrl-200) / var(--_bg-alpha));
        border: 1px solid hsla(var(--hsl-ntrl-200) / var(--_bd-alpha));
        color: var(--clr-ntrl-200);
    }

    &--default-gray {
        background-color: var(--clr-ntrl-100);
        border: 1px solid var(--clr-ntrl-300);
        color: var(--clr-ntrl-400);
    }

    &[disabled] {
        background-color: var(--clr-ntrl-100) !important;
        color: var(--clr-ntrl-300) !important;
        border: 1px solid currentColor;
        pointer-events: none;
    }

    @include bp-up('tablet') {
        font-size: .875rem;
    }
}

button.btn,
a.btn {
    transition-property: color, background-color, border-color;
    transition-duration: .2s;

    @media (hover: hover) {
        &:hover {
            background-color: var(--clr-grn-300);
            color: var(--clr-grn-100);
        }
    }

    &:active {
        transition: none;
        background-color: var(--clr-grn-200);
        color: var(--clr-grn-300);
    }

    &--transparent {
        @media (hover: hover) {
            &:hover {
                --_bg-alpha: .3;
                --_bd-alpha: .6;
                background-color: hsla(var(--hsl-ntrl-200) / var(--_bg-alpha));
                border: 1px solid hsla(var(--hsl-ntrl-200) / var(--_bd-alpha));
                color: var(--clr-ntrl-100);
            }
        }

        &:active {
            --_bg-alpha: .6;
            --_bd-alpha: 1;
            background-color: hsla(var(--hsl-ntrl-200) / var(--_bg-alpha));
            border: 1px solid hsla(var(--hsl-ntrl-200) / var(--_bd-alpha));
            color: var(--clr-ntrl-100);
        }
    }

    &--default-gray {
        @media (hover: hover) {
            &:hover {
                background-color: var(--clr-ntrl-200);
                border-color: currentColor;
                color: var(--clr-ntrl-400);
            }
        }

        &:active {
            background-color: var(--clr-ntrl-400);
            border-color: currentColor;
            color: var(--clr-ntrl-200);
        }
    }
}