@use 'colors' as *;

:root {
    //colors
    @each $hue, $shades in $colors {
        @each $shade, $color in $shades {
            --clr-#{$hue}-#{$shade}: #{$color};
        }
    }

    @each $hue, $shades in $colorsHSL {
        @each $shade, $color in $shades {
            --hsl-#{$hue}-#{$shade}: #{$color};
        }
    }
}