@use '../alias/mixins' as *;

.person {
    display: flex;
    align-items: center;
    gap: 1rem;

    &__img {
        --_aspect-ratio: 2.5rem;
    }

    &__info {
        font-weight: 500;
    }

    &__name {
        @include clampText(.875rem, 3vw, 1.125rem);
        line-height: 1.125;
    }

    &__desc {
        margin-top: .25rem;
        line-height: 1.5;
    }
}