@use '../alias/medias' as *;

.input-form {
    --_max-w: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding: .25rem;
    width: 100%;
    max-width: var(--_max-w);
    background-color: var(--clr-ntrl-100);
    border-radius: 2rem;
    border: 1px solid var(--clr-ntrl-300);

    &__label {
        display: flex;
        align-items: center;
        gap: 1rem;
        flex: 1;
        padding-left: clamp(1rem, 3vw, 1.5rem);
    }

    &__icon {
        --_aspect-ratio: 1.5rem;
        width: var(--_aspect-ratio);
        height: var(--_aspect-ratio);
        font-size: 0;

        svg {
            fill: currentColor;
        }
    }

    &__input {
        width: 100%;
    }

    &--btn-out {
        @include bp-down('phone') {
            &.input-form {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                gap: 1rem;
                padding: 0;
                width: 100%;
                max-width: unset;
                background-color: unset;
                border-radius: 0;
                border: none;
            }
    
            .input-form {
                &__label {
                    display: flex;
                    align-items: center;
                    gap: 1rem;
                    flex: 1 0 100%;
                    padding: .5rem .75rem;
                    width: 100%;
                    max-width: var(--_max-w);
                    background-color: var(--clr-ntrl-100);
                    border-radius: 2rem;
                    border: 1px solid var(--clr-ntrl-300);
                }
    
                &__icon {
                    --_aspect-ratio: 1.5rem;
                    width: var(--_aspect-ratio);
                    height: var(--_aspect-ratio);
                    font-size: 0;
    
                    svg {
                        fill: currentColor;
                    }
                }
    
                &__input {
                    width: 100%;
                }
            }
        }
    }
}