@use './alias/mixins' as *;
.container {
    // Have no brain to get better solution - using magic numbers for scroll width
    // all below in px
    // 120 = --_hero-pd-top 
    // 1490 = --_big-container-w + --_container-both-gap-width + --scroll-width
    // 1250 = --_def-container-w + --_container-both-gap-width + --scroll-width
    /* value generated by mixin 'adaptiveValue'*/ 
    @include adaptiveValue(
        --_out-of-container-gap, 
        120, 
        0, 
        1490, 
        1250
    );
    --_big-container-w: 90rem;
    --_def-container-w: 75rem;
    --_container-gap-width: 1rem;
    --_container-both-gap-width: calc(var(--_container-gap-width) * 2);

    --_ignore-container-one-side-gap-width: calc(100% + var(--_container-gap-width));
    --_ignore-container-both-side-gap-width: calc(100% + var(--_container-both-gap-width));

    width: min(calc(100% - var(--_container-both-gap-width)), var(--_def-container-w));
    margin-inline: auto;
}