@use '../alias/mixins' as *;
@use '../alias/medias' as *;

.hero {
    --_hero-pd-top: 5.5rem;
    --_hero-desc-margin-inline: auto;
    display: grid;
    grid-template-rows: max-content minmax(25vh, 1fr);
    gap: 2rem;
    text-align: center;
    padding-top: var(--_hero-pd-top);
    min-height: 100dvh;

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: clamp(1.25rem, 3vw, 2rem);
    }

    &__desc {
        >* {
            margin-inline: var(--_hero-desc-margin-inline);
        }
    }

    &__title {
        margin-bottom: .5em;
        max-width: 15ch;
        text-transform: capitalize;
        @include clampText(1.75rem, 5vw, 2.5rem);
        font-weight: 600;
    }

    &__subtitle {
        max-width: 50ch;
    }

    .input-form {
        &__icon {
            color: var(--clr-ylw-100);
        }
    }

    &__preview {
        position: relative;
        display: grid;
        min-height: 25vh;
        box-sizing: content-box;
    }

    &__img {
        grid-column: 1;
        grid-row: 1;
        position: absolute;
        top: 0;
        bottom: 0;
        margin-left: calc(var(--_container-gap-width) * -1);
        width: var(--_ignore-container-both-side-gap-width);
        overflow: hidden;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
        }
    }

    &__stats {
        --_border-radius: 2rem;
        grid-column: 1;
        grid-row: 1;
        position: absolute;
        top: 0;
        bottom: 0;
        left: calc(var(--_container-gap-width) * -1);
        margin-block: auto;
        width: var(--_ignore-container-both-side-gap-width);
        height: max-content;
        z-index: 1;
        overflow: hidden;
    }

    .partners {

        &__title {
            margin-bottom: 1em;
        }

        &__list {
            --_gap-width: 1rem;
            --_column-count: 2;
            --_flex-basis: calc(100% / var(--_column-count) - var(--_gap-width));
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            gap: var(--_gap-width);
        }

        &__partner {
            flex: 0 1 var(--_flex-basis);
        }
    }

    .stats {

        &__list {
            --_gap-width: 1.5rem;
            display: flex;
            width: max-content;
            gap: var(--_gap-width);
            animation: statsFlow 30s infinite linear;
        }

        &__stat {
            display: flex;
            align-items: center;
            gap: 1rem;
            padding: clamp(.75rem, 3vw, 1.5rem);
            background-color: var(--clr-ntrl-100);
            border-radius: var(--_border-radius);
        }

        &__img {
            display: flex;
            align-items: center;

            >*:not(:first-child) {
                margin-left: calc(var(--_aspect-ratio) / 2 * -1);
            }

            >.bubble {
                --_aspect-ratio: clamp(2.5rem, 5vw, 3.5rem);
            }
        }

        &__desc {
            font-size: .75rem;
            line-height: 1.25;
        }
    }

    @include bp-up('phone') {
        --_hero-desc-margin-inline: 0;
        text-align: start;

        &__content {
            align-items: start;
            margin-inline: auto;
            max-width: 25rem;
        }

        .partners {
            &__list {
                --_column-count: 4;
                justify-content: start;
            }
        }
    }

    @include bp-up('phablet') {
        --_hero-pd-top: 7.5rem;
        grid-template-columns: 1fr .75fr;
        grid-template-rows: auto;

        &__content {
            margin-top: 2rem;
            margin-inline: 0;
            max-width: unset;
        }

        &__img {
            margin-top: calc(var(--_hero-pd-top) * -1);
            margin-left: 0;
            margin-right: calc(var(--_container-gap-width) * -1);
            width: calc(100% + var(--_container-gap-width) + var(--_out-of-container-gap));
            height: calc(100% + var(--_hero-pd-top));
            border-bottom-left-radius: 5rem;
            overflow: hidden;
        }

        &__stats {
            --_from-left: 3rem;
            top: unset;
            bottom: 3rem;
            left: var(--_from-left);
            right: calc(var(--_container-gap-width) * -1);
            margin-block: 0;
            width: calc(100% + var(--_container-gap-width) + var(--_out-of-container-gap) - var(--_from-left));
            border-top-left-radius: var(--_border-radius);
            border-bottom-left-radius: var(--_border-radius);
        }
    }

    @include bp-up('tablet') {
        grid-template-columns: 1fr 1fr;

        &__content {
            margin-top: 4rem;
        }
    }

    @include bp-up('desktop') {
        aspect-ratio: 16 / 9;
        min-height: auto;
    }
}

@keyframes statsFlow {
    from {
        translate: 0 0;
    }

    to {
        translate: calc(-50% - var(--_gap-width) / 2) 0;
    }
}