@use '../alias/medias' as *;

.testimonials-slider {
    padding-bottom: 2rem;

    &__btns {
        position: absolute;
        inset: 0;
        height: fit-content;
        top: 15%;
        display: flex;
        justify-content: space-between;
    }

    &__list {
        gap: 2rem;
    }

    &__slide {
        opacity: .5;
        transition: opacity .4s;

        &[slider-slide='active'] {
            opacity: 1;
        }
    }

    &__img {
        width: 100%;
        height: auto;
        aspect-ratio: 2 / 1;
        border-radius: .5rem;
        margin-bottom: clamp(1rem, 3vw, 1.75rem);
    }

    &__content {
        background-color: var(--clr-ntrl-100);
    }

    &__title {
        margin-bottom: clamp(.5rem, 3vw, 1rem);
        font-size: 1.25rem;
        line-height: 1.25;
    }

    &__text {
        margin-bottom: clamp(1rem, 3vw, 1.5rem);
        max-height: 12ch;
        overflow-y: scroll;
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        gap: 1rem;
    }

    &__rate {
        display: flex;
        align-items: center;
        gap: 1rem;

        svg {
            fill: var(--clr-ylw-100);
        }
    }

    @include bp-up('phone') {
        &__btns {
            top: 25%;
        }
    }

    @include bp-up('phablet') {
        &__slide {
            --_animation-time: .4s;
            --_bottom-gap: 6rem;
            display: grid;
            padding-bottom: var(--_bottom-gap);
            transition-duration: var(--_animation-time);

            &[slider-slide='active'] {
                .testimonials-slider {
                    &__content {
                        margin-bottom: calc(var(--_bottom-gap) / 2 * -1);
                        box-shadow: 0 .5rem 2rem 0 hsla(var(--hsl-ntrl-700) / .2);
                        border-radius: 1rem;
                    }
                }
            }
        }

        &__img {
            grid-column: 1;
            grid-row: 1;
        }

        &__content {
            grid-column: 1;
            grid-row: 1;
            z-index: 1;
            justify-self: center;
            align-self: end;
            width: calc(100% - 4rem);
            max-width: 37rem;
            border-top-left-radius: 1rem;
            border-top-right-radius: 1rem;
            padding: clamp(1rem, 3vw, 2rem);
            transition-property: margin-bottom, box-shadow;
            transition-duration: var(--_animation-time);
        }
    }

