@use '../alias/medias' as *;
@use '../alias/mixins' as *;

.contact-us {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: clamp(1.25rem, 6vw, 4rem) clamp(.75rem, 6vw, 4rem);
    background-image: url('../img/jpg/abstract-bg.jpg');
    background-position: center 50%;
    background-size: 100%;
    border-radius: clamp(1.5rem, 3vw, 2rem);
    isolation: isolate;
    animation-name: contactUsBgFlow;
    animation-duration: 15s;
    animation-timing-function: linear;
    animation-direction: alternate;
    animation-iteration-count: infinite;

    &__title {
        max-width: 21ch;
        text-align: center;
        @include clampText(1.25rem, 3vw, 2rem);
        margin-bottom: 1em;
        line-height: 1.5;
    }

    .input-form {
        &__icon {
            color: var(--clr-bl-200);
        }
    }

    &__bubbles {
        position: absolute;
        inset: 0;
        z-index: -1;

        >* {
            --_move-up: -10%;
            --_animation-time: 2s;
            --_animation-delay: 0s;
            position: absolute;
            animation-name: contactUsBubbleFloat;
            animation-duration: var(--_animation-time);
            animation-delay: var(--_animation-delay);
            animation-timing-function: ease-in-out;
            animation-direction: alternate;
            animation-iteration-count: infinite;

            &:nth-child(1),
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(8) {
                --_border-radius: 1.5rem;
            }

            &:nth-child(1),
            &:nth-child(5) {
                --_animation-time: 8s;
            }

            &:nth-child(4),
            &:nth-child(8) {
                --_animation-time: 4s;
            }

            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(6),
            &:nth-child(7) {
                --_move-up: -15%;
            }

            &:nth-child(3),
            &:nth-child(6) {
                --_move-up: -25%;
                --_animation-delay: 1s;
            }

            &:nth-child(1) {
                @include adaptiveValue('--_side-gap-width', 0, 45, 1440, 480);
                --_aspect-ratio: 5.125rem;
                --_from-side: 2.75rem;
                top: 12%;
                left: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(2) {
                @include adaptiveValue('--_side-gap-width', 0, 90, 1440, 480);
                --_aspect-ratio: 2.25rem;
                --_from-side: 12.75rem;
                top: 24%;
                left: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(3) {
                @include adaptiveValue('--_side-gap-width', 0, 45, 1440, 480);
                --_aspect-ratio: 2.25rem;
                --_from-side: 4rem;
                top: 57%;
                left: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(4) {
                @include adaptiveValue('--_side-gap-width', 0, 120, 1440, 480);
                --_aspect-ratio: 3.25rem;
                --_from-side: 12rem;
                top: 58%;
                left: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(5) {
                @include adaptiveValue('--_side-gap-width', 0, 90, 1440, 480);
                --_aspect-ratio: 4.5rem;
                --_from-side: 12.5rem;
                top: 14%;
                right: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(6) {
                @include adaptiveValue('--_side-gap-width', 0, 90, 1440, 480);
                --_aspect-ratio: 2.25rem;
                --_from-side: 5rem;
                top: 35%;
                right: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(7) {
                @include adaptiveValue('--_side-gap-width', 0, 90, 1440, 480);
                --_aspect-ratio: 2.25rem;
                --_from-side: 12.5rem;
                top: 47%;
                right: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }

            &:nth-child(8) {
                @include adaptiveValue('--_side-gap-width', 0, 90, 1440, 480);
                --_aspect-ratio: 3.5rem;
                --_from-side: 4.5rem;
                top: 63%;
                right: max(calc(var(--_from-side) - var(--_side-gap-width)), 1rem);
            }
        }

        @include bp-down('tablet') {
            >* {
                display: none;
            }
        }

        @include bp-down('laptop') {
            >* {
                &:nth-child(1) {
                    --_from-side: 4rem;
                }

                &:nth-child(3) {
                    --_from-side: 6rem;
                }

                &:nth-child(6) {
                    top: 20%;
                    --_from-side: 8rem;
                }

                &:nth-child(8) {
                    top: 55%;
                    --_from-side: 6rem;
                }


                &:nth-child(2) {
                    display: none;
                }

                &:nth-child(4) {
                    display: none;
                }

                &:nth-child(5) {
                    display: none;
                }

                &:nth-child(7) {
                    display: none;
                }
            }
        }
    }
}

@keyframes contactUsBgFlow {
    from {
        background-position: 0 10%;
    }

    to {
        background-position: 0 90%;
    }
}

@keyframes contactUsBubbleFloat {
    from {
        translate: 0 10%;
    }

    to {
        translate: 0 var(--_move-up, -10%);
    }
}