.fancy-box {
    inset: 0;
    margin: auto;
    opacity: 0;
    background: black;
    border: none;
    animation: fancyIn .5s forwards;
    outline: none;
    width: min(calc(100% - clamp(2rem, 24vw - 4rem, 24rem)), 900px);
    aspect-ratio: 9 / 6;
}   

.fancy-box * {
    outline: none;
}

.fancy-box > * {
    position: absolute;
   width: 100%;
   height: 100%; 
   object-fit: contain;
}

.fancy-box::backdrop {
    animation: backgroundIn .5s forwards;
    cursor: pointer;
}

.fancy-box--out {
    animation: fancyOut .5s forwards;
}

.fancy-box--out::backdrop {
    animation: backgroundOut .5s forwards;
    cursor: default;
} 

@keyframes fancyIn {
    from {
        scale: .95;
        translate: 0 5%;
        opacity: 0;
    }
    to {
        scale: 1;
        translate: 0 0;
        opacity: 1;

    }
}

@keyframes backgroundIn {
    from {
        background: rgba(0 0 0 / .1);
    }
    to {
        background: rgba(0 0 0 / .7);
    }
}

@keyframes fancyOut {
    from {
        scale: 1;
        translate: 0 0;
        opacity: 1;
    }
    to {
        scale: .9;
        translate: 0 10%;
        opacity: 0;
    }
}

@keyframes backgroundOut {
    from {
        background: rgba(0 0 0 / .7);
    }
    to {
        background: rgba(0 0 0 / 0);
    }
}