@use '../alias/medias' as *;

.article {
    display: flex;
    flex-direction: column;
    gap: clamp(1rem, 3vw, 1.5rem);

    &__img {
        width: 100%;
        aspect-ratio: 3 / 2;
        border-radius: 1rem;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
    }

    &__text {
        margin-bottom: clamp(.75rem, 3vw, 1rem);
    }

    &__title {
        font-size: 1.125rem;
        line-height: 1.5;
    }

    &__desc {
        margin-top: .75rem;
    }

    &__author {
        margin-bottom: .75rem;
        .person {
            &__img {
                --_aspect-ratio: 2rem;
            }
        }
    }

    &__timing {
        display: flex;
        align-items: center;
        gap: .75rem;

        svg {
            fill: var(--clr-ntrl-400);
        }
    }

    @include bp-up('phone') {
        flex-direction: row;
        align-items: center;
        max-width: 45rem;
        margin-inline: auto;
        width: 100%;

        &__img {
            flex: .75 1;
            height: 100%;
            aspect-ratio: unset;
        }

        &__content {
            flex: 1;
        }

        &__desc {
            display: none;
        }

        &__author {
            order: -1;
        }
    }
}