@use '../alias/medias' as *;
@use '../alias/mixins' as *;

.recommend {
    &__header {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
        margin-bottom: clamp(1.5rem, 3vw, 2.5rem);
    }

    &__filters {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: .5rem .75rem;
    }

    &__filter {
        &:where(.active) {
            background-color: var(--clr-grn-100);
            color: var(--clr-grn-200);

            @media (hover: hover) {
                &:hover {
                    background-color: var(--clr-grn-200);
                    color: var(--clr-grn-100);
                }
            }
        }

        &:is(&:active) {
            &.active {
                background-color: var(--clr-ntrl-300);
                color: var(--clr-ntrl-100);
            }

            background-color: var(--clr-grn-200);
            color: var(--clr-grn-100);
        }
    }

    &__slider {
        position: relative;

        &-btns {
            &--header {
                display: none;
            }

            &--slider {
                position: absolute;
                display: flex;
                justify-content: space-between;
                top: 0;
                bottom: 0;
                left: -.5rem;
                right: -.5rem;
                margin-block: auto;
                height: min-content;
                z-index: 1;
            }
        }
    }

    @include bp-up('tablet') {
        &__header {
            flex-direction: row;
            justify-content: space-between;
            align-items: end;
            gap: 1rem;
        }

        &__slider {
            position: relative;

            &-btns {
                &--header {
                    display: flex;
                    gap: 1rem;
                }

                &--slider {
                    display: none;
                }

                > button {
                    box-sizing: border-box;
                }
            }
        }
    }

    @include bp-up('laptop') {
        margin-left: 2.5rem;
    }
}