html {
    scroll-behavior: smooth;
    scroll-padding-top: 8rem;
}

body {
    font-family: 'Poppins';
    color: var(--clr-ntrl-800);

    > footer,
    > main > section {
        margin-bottom: clamp(2.5rem, 10vw - 1rem, 7.5rem);
    }
}

