.text {
    font-size: 1rem;
    line-height: 1.75;
    color: var(--clr-ntrl-600);

    &--ghost {
        opacity: .75;
    }

    &--bold {
        font-weight: 600;
    }

    &--dark {
        color: var(--clr-ntrl-800)
    }

    &--small {
        font-size: .875rem;
    }

    &--capitalize {
        text-transform: capitalize;
    }

    &--hover {
        @media (hover: hover) {
            transition-property: filter opacity;
            transition-duration: .2s;

            &:hover {
                opacity: .75;
                filter: brightness(2);
            }
        }
    }
}

.slider {
    &__wrapper {
        overflow: hidden;
    }
}

.bubble {
    --_aspect-ratio: 2rem;
    --_border-radius: 50%;
    width: var(--_aspect-ratio);
    height: var(--_aspect-ratio);
    border-radius: var(--_border-radius);
    border: .1em solid var(--clr-ntrl-100);
    
    &--without-rim {
        border: none;
    }
}

.frame-text {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
}

.center-with-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

