@use '../alias/medias' as *;

.footer {
    &__nav {
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-right: auto;
        max-width: 20rem;
    }

    &__col {
        display: flex;
        flex-direction: column;
        flex: 1 1 15rem;
        max-width: fit-content;

        &:nth-child(2) {
            margin-right: auto;
        }
    }

    &__link {
        position: relative;
        padding-bottom: .1em;
        width: fit-content;
        line-height: 1;
        transition: color .2s;
        overflow: hidden;

        &:not(:last-child) {
            margin-bottom: 1.25rem;
        }

        &:not(:first-child) {
            color: var(--clr-ntrl-400);
        }

        &:first-child {
            margin-bottom: 1.5rem;
            font-size: 1.125rem;
        }

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 0;
            height: 1px;
            background-color: currentColor;
            transition: width .3s;
        }

        @media (hover: hover) {
            &:hover {
                &:not(:first-child) {
                    color: var(--clr-ntrl-700);
                }

                &::before {
                    width: 100%;
                }
            }
        }  
    }

    @include bp-up('laptop') {
        &__info {
            margin-right: auto;
        }

        &__col {
            &:nth-child(2) {
                margin-right: 0;
            }
        }
    }
}

.socials {
    display: flex;
    align-items: center;
    gap: clamp(1rem, 3vw, 1.5rem);

    &__link {
        --_aspect-ratio: 2rem;
        width: var(--_aspect-ratio);
        height: var(--_aspect-ratio);

        &--facebook {
            color: var(--clr-bl-300);
        }
        &--twitter {
            color: var(--clr-bl-200);
        }
        &--instagram {
            color: var(--clr-ylw-100);
        }
        
        svg {
            fill: var(--clr-ntrl-700);
            transition: fill .2s;
        }

        @media (hover: hover) {
            &:hover {
                svg {
                    fill: currentColor;
                }
            }
        }
    }
}