@use '../alias/medias' as *;

@mixin headerStickingLink {
    background-color: hsla(var(--hsl-ntrl-700) / var(--_bg-alpha));
    border-color: hsla(var(--hsl-ntrl-700) / var(--_bd-alpha));
    color: var(--clr-ntrl-700);

    @media (hover: hover) {
        &:hover {
            background-color: hsla(var(--hsl-ntrl-700) / var(--_bg-alpha));
            border-color: hsla(var(--hsl-ntrl-700) / var(--_bd-alpha));
            color: var(--clr-ntrl-200);
        }
    }

    &:active {
        background-color: hsla(var(--hsl-ntrl-700) / var(--_bg-alpha));
        border-color: hsla(var(--hsl-ntrl-700) / var(--_bd-alpha));
        color: var(--clr-ntrl-100);
    }
}

.header {
    --_header-pd-block-max: 2.5rem;
    --_header-pd-block-min: 1rem;
    position: fixed;
    top: 0;
    padding-block: clamp(var(--_header-pd-block-min), 3vw, var(--_header-pd-block-max));
    width: 100%;
    transition-property: background, box-shadow, padding;
    transition-duration: .4s;
    z-index: 10;

    &[sticking] {
        --_header-pd-block-max: 1.5rem;
        /* From https://css.glass */
        background: rgba(255, 255, 255, .75);
        box-shadow: 0 4px 30px rgba(0, 0, 0, .1);
        backdrop-filter: blur(5px);
        -webkit-backdrop-filter: blur(10px);
        border-bottom: 1px solid rgba(255, 255, 255, .4);
    }

    &__inner {
        display: flex;
        align-items: center;
    }

    &__burger {
        --_flex-direction: column;
        margin-left: auto;
    }

    &__nav {
        display: flex;
        flex-direction: var(--_flex-direction);
        align-items: center;
        gap: clamp(.75rem, 3vw, 1.5rem);
    }

    &__link.btn {
        --_pd-block: .5rem;

        @media ( 768px <= width <= 1000px) {
            @include headerStickingLink;
        }
    }

    &__cta {
        --_margin-gap: clamp(1.5rem, 3vw, 2.5rem);
        margin-top: var(--_margin-gap);
    }

    .burger {
        &__btn {
            position: relative;
            z-index: 1;
        }

        &__inner {
            --_pd-block: calc(20% + 3vh);
            position: fixed;
            display: flex;
            justify-content: center;
            top: 0;
            left: 100%;
            right: 0;
            padding-top: var(--_pd-block);
            width: 100vw;
            height: 100%;
            // for IE
            min-height: 100vh;
            min-height: 100dvh;
            background-color: var(--clr-grn-200);
            transition: left .4s;
            overflow-y: auto;
        }

        &__content {
            --_display-option: flex;
            flex-direction: var(--_flex-direction);
            align-items: center;
            height: fit-content;
            padding-block: 2rem;
        }

        &.active {
            .burger {
                &__inner {
                    left: 0;
                }

                &__content {
                    --_display-option: flex;
                    
                }
            }
        }
    }

    .dropdown {
        --_gap-width: 1rem;
        --_transition-time: .4s;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        &__btn {
            svg {
                rotate: -90deg;
                transition: rotate var(--_transition-time);
            }
        }

        &__list {
            display: grid;
            grid-template-rows: 0fr;
            margin-top: var(--_gap-width);
            transition: grid-template-rows var(--_transition-time);
        }

        &__list-wrap {
            display: flex;
            flex-direction: column;
            gap: 1.25rem;
            padding-inline: .75rem;
            background-color: var(--clr-ntrl-100);
            border-radius: 1rem;
            border: 1px solid var(--clr-ntrl-800);
            transition-property: padding, opacity;
            transition-duration: var(--_transition-time);
            overflow: hidden;
            opacity: 0;
        }
    
        &.active {
            .dropdown {
                &__btn {
                    svg {
                        rotate: -270deg;
                    }
                }
                &__list {
                    grid-template-rows: 1fr;
                }
                &__list-wrap {
                    padding-block: 1rem;
                    font-weight: 500;
                    opacity: 1;
                }
            }
        }
    }

    @include bp-up('phone') {
        .burger {
            &__inner {
                min-height: unset;
                height: auto;
                padding-block: var(--_pd-block);
            }
        }
    }

    @include bp-up('tablet') {
        &[sticking] {
            .header {
                &__link.btn {
                    @include headerStickingLink;
                }
            }
        }
        &__burger {
            --_flex-direction: row;
        }
        &__cta {
            margin-top: 0;
            margin-left: var(--_margin-gap);
        }
        .burger {
            &__inner {
                position: static;
                padding: 0;
                width: unset;
                background-color: unset;
                overflow: unset;
            }

            &__content {
                padding: 0;
            }
        }
        .dropdown {
            &__list {
                position: absolute;
                top: calc(100% + var(--_gap-width));
                left: 0;
                right: 0;
                margin-top: 0;
                width: 100%;

                &::before {
                    content: '';
                    position: absolute;
                    top: calc(var(--_gap-width) * -1);
                    left: 0;
                    right: 0;
                    height: 0;
                }
            }

            &.active {
                .dropdown {
                    &__list {
                        &::before {
                            height: var(--_gap-width);
                        }
                    }
                }
            }
        }
    }
}