@use '../alias/medias' as *;

.articles {
    display: flex;
    flex-direction: column;
    align-items: center;

    .section-heading {
        margin-bottom: clamp(1rem, 3vw, 1.5rem);

        &__title {
            max-width: 27rem;
        }
    }

    &__cta {
        margin-bottom: clamp(1.5rem, 3vw, 2.5rem);
    }
    

    &__grid {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        gap: 2rem;
    }

    @include bp-up('laptop') {
        &__grid {
            grid-template-columns: 1fr 1fr;
            grid-template-rows: repeat(3, 1fr);
            gap: 2.5rem 4rem;
        }

        &__article {
            &.article {
                &:first-child {
                    grid-column: 2;
                    grid-row: 1 / -1;

                    flex-direction: column;

                    align-items: start;

                    .article {
                        &__img {
                            aspect-ratio: 2 / 1;
                            height: unset;
                            flex: 1;
                            // flex: unset;
                        }

                        &__content {
                            // flex: unset;
                        }
                        &__title {
                            font-size: 1.5rem;
                        }
                        &__desc {
                            display: block;
                        }
                    }
                }

            }
        }
    } 
}