@use '../alias/medias' as *;
@use '../alias/mixins' as *;

.recommend-slider {

    &__wrapper {
        width: 100%;
    }

    &__slides {
        display: grid;
        gap: 1rem;
        width: fit-content;
        grid-template-columns: repeat(auto-fit, 340px);
        grid-auto-flow: column;
    }

    &__slide {
        display: flex;
        flex-direction: column;
        gap: clamp(.75rem, 3vw, 1.5rem);
        flex: 1;
        // max-width: 340px;
    }

    &__img {
        position: relative;
        border-radius: 1.5rem;
        overflow: hidden;
        aspect-ratio: 1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__tag {
        --_pd-block: .5rem;
        position: absolute;
        gap: .5rem;
        bottom: 1rem;
        left: 1rem;
        text-transform: capitalize;
        z-index: 1;

        &--popular {
            background-color: var(--clr-rd-100);
            color: var(--clr-rd-200);
        }

        &--new-house {
            background-color: var(--clr-bl-100);
            color: var(--clr-bl-300);
        }

        &--best-deal {
            background-color: var(--clr-grn-100);
            color: var(--clr-grn-300);
        }
    }

    &__info {
        font-weight: 500;
    }

    &__slide-title {
        @include clampText(1.25rem, 3vw, 1.5rem);
    }

    &__price {
        @include clampText(1rem, 3vw, 1.25rem);
        line-height: 1.5;
    }

    @include bp-up('tablet') {
        &__wrapper {
            width: calc(100% + var(--_out-of-container-gap) + var(--_container-gap-width));
        }
        &__img {
            aspect-ratio: 8 / 9;
        }
    }
}