@use '../alias/medias' as *;

.ready-to-sell {
    display: grid;
    grid-template-rows: 1fr max-content;
    place-items: center;
    gap: 2rem;

    &__content {
        --_text-align: center;
        --_max-width: 30rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        margin-inline: auto;
        width: 100%;
        max-width: var(--_max-width);
    }

    &__desc {
        text-align: var(--_text-align);
    }

    &__details {
        text-align: var(--_text-align);
        width: 100%;
        max-width: 27.5rem;

        h3 {
            margin-bottom: 1rem;
        }
    }

    &__details-list {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(min(100%, 9.5rem), 1fr));
        place-items: center;
        gap: 1rem;
    }

    &__detail {
        display: flex;
        align-items: center;
        gap: 1rem;
        width: fit-content;
        white-space: nowrap;
        color: var(--clr-ntrl-700);
        font-weight: 500;

        svg {
            fill: var(--clr-ntrl-400);
        }
    }

    &__footer {
        --_from-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        margin-top: var(--_from-top);

        .person {
            &__img {
                --_aspect-ratio: 3rem;
            }
        }
    }

    &__preview {
        display: grid;
    }

    &__video {
        grid-column: 1;
        grid-row: 1;
        position: relative;
        place-self: center;
        width: 90%;
        height: 90%;
        border-radius: .25rem;
        overflow: hidden;

        &-play {
            --_hover-pump: 0rem;
            position: absolute;
            inset: 0;
            margin: auto;
            width: 1em;
            height: 1em;
            background-color: hsla(var(--hsl-ntrl-100) / .4);
            border-radius: 50%;
            color: var(--clr-ntrl-100);
            font-size: clamp(calc(2.5rem + var(--_hover-pump)), 6vw, calc(3rem + var(--_hover-pump)));
            backdrop-filter: blur(3px);

            svg {
                margin-right: -.05em;
                width: .5em;
                height: auto;
                fill: currentColor;
                stroke: currentColor;
                filter: drop-shadow(0 0 5px currentColor);
            }
        }

        img {
            position: relative;
            width: 100%;
            filter: brightness(.8);
            z-index: -1;
        }

        @media (hover: hover) {
            --_transition-time: .4s;

            &-play {
                box-shadow: 0 0 0 currentColor;
                transition: box-shadow var(--_transition-time);
            }

            img {
                transition: filter var(--_transition-time);
            }

            &:hover {
                .ready-to-sell__video {
                    &-play {
                        box-shadow: 0 0 1rem currentColor;
                    }
                }

                img {
                    filter: brightness(.85);
                }
            }
        }
    }

    &__gallery {
        grid-column: 1;
        grid-row: 1;
        display: flex;
        align-items: end;
        align-self: flex-end;
        justify-content: center;
        gap: .5rem;
        width: 100%;
        z-index: 1;

        &-img {
            flex: 1 1 30%;
            aspect-ratio: 3 / 2;
            border-radius: .25rem;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
            }

        }
    }


    @include bp-up('phablet') {
        &__content {
            --_text-align: start;
        }

        &__details {
            align-self: flex-start;
        }

        &__details-list {
            place-items: start;
        }

        &__footer {
            justify-content: space-between;
            width: 100%;
        }

        &__preview {
            max-width: 32rem;
        }
    }

    @include bp-up('tablet') {
        grid-template-columns: 1fr .75fr;
        grid-template-rows: auto;
        place-items: center;
        margin-left: 2.5rem;

        &__content {
            align-items: start;
            max-width: unset;
        }

        &__desc {
            max-width: 30rem;
        }

        &__footer {
            position: relative;
            gap: .875rem;
            padding-top: clamp(var(--_from-top), 3vw, calc(var(--_from-top) * 2));
            max-width: 27.5rem;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                width: 100%;
                height: 1px;
                background-color: var(--clr-ntrl-300);
            }

            .person {
                gap: .75rem;
            }
        }

        &__cta {
            gap: .75rem;
        }

        &__preview {
            aspect-ratio: 1.15 / 1;
        }

        &__video {
            place-self: start;
            justify-self: end;
            width: calc(100% - 2.5rem);
            height: calc(100% - 2.5rem);

            img {
                width: 100%;
                height: 100%;
            }
        }

        &__gallery {
            width: calc(100% - 1.25rem);
            

            &-img {
                flex: 0 1 20%;
                aspect-ratio: 2 / 1.6;
                transition-property: flex, aspect-ratio;
                transition-duration: .6s;
                transition-delay: .1s;

                &:first-child {
                    flex: 1;
                    aspect-ratio: 3 / 2;
                }

                @media (hover: hover) {
                    &:hover {
                        flex: 1;
                        aspect-ratio: 3 / 2;
                    }
                }
            }
        }
        @media (hover: hover) {
            &:has(&__gallery-img:not(:first-child):hover) {
                .ready-to-sell__gallery {
                    &-img {
                        &:first-child {
                            flex: 0 1 20%;
                            aspect-ratio: 2 / 1.6;
                        }
                    }
                }
            }
        }
    }
}